html {
  font-family: 'Lato', sans-serif;
}

.animated {
  animation-duration: .5s;
}

#App {
  height: 100vh;
  
}

.container {
  text-align: center;
  
}

#page-wrap {
  text-align: center;
  
}

.bm-item {
  display: inline-block;
  text-decoration: none;
  margin-bottom: 10px;
  color: #595859;
  transition: color 0.2s;
  font-size: 1.5em;
}

.menu-img {
  margin-right: 25px;
}

.bm-item:hover {
  color: rgba(31, 181, 144);
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

.bm-burger-bars {
  background-color: rgba(244, 158, 9);
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #595859;
  
}

.bm-menu {
  background-color: rgba(244, 158, 9, 0.9);
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

