/* https://leaverou.github.io/bubbly/ */
#SpeechBubble {
	text-align: center;
	font-size: 1.5em;
	position: absolute;
	left: 400px;
	background: rgba(79, 182, 168, 0.438);
	border-radius: .9em;
	background-image: linear-gradient(to bottom, rgba(92, 180, 168, 0.541) 0%, rgba(146, 224, 214, 0.733) 100%);
	min-width: 300px;
	padding: 1em;
}

#SpeechBubble.AnimatingBubble {
	-webkit-animation-name: bounce;
	        animation-name: bounce;
	-webkit-animation-duration: 0.7s;
	        animation-duration: 0.7s;
}

#SpeechBubble :after {
	overflow: hidden;
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	width: 0;
	height: 0;
	border: 2em solid transparent;
	border-top-color: rgba(31, 181, 161, 0.438);
	border-bottom: 0;
	border-left: 0;
	margin-left: -1em;
	margin-bottom: -2em;
}
/* creates opening quotation mark */
#SpeechBubble:before {
  content: "\201C";
  position: absolute;
  z-index: 1;
  top: 10px;
  left: 5px;
  font: 60px/1 Georgia, serif;
  color:rgba(12, 68, 61, 0.966);
}

/* creates closing quotation mark */
#SpeechBubble:after {
  content: "\201D";
  position: absolute;
  z-index: 1;
  bottom: 0;
  right: 5px;
  font: 60px/0.25 Georgia, serif;
  color:rgba(12, 68, 61, 0.966);
}


@-webkit-keyframes bounce {
	0% {
	  -webkit-transform: scale(0);
	          transform: scale(0);
	}
	50% {
	  -webkit-transform: scale(1.2);
	          transform: scale(1.2);
	}
	100% {
	  -webkit-transform: scale(1);
	          transform: scale(1);
	}
	}


@keyframes bounce {
	0% {
	  -webkit-transform: scale(0);
	          transform: scale(0);
	}
	50% {
	  -webkit-transform: scale(1.2);
	          transform: scale(1.2);
	}
	100% {
	  -webkit-transform: scale(1);
	          transform: scale(1);
	}
	}

